<template>
  <div id="vue-blink" :style="blinkStyling">
      <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'vue-blink',
  props: {
    duration: {
      type: Number,
      required: false,
      default: 5
    },
    iterationCount: {
      required: false,
      default: 'infinite'
    }
  },
  computed: {
    blinkStyling () {
      return {
        animationName: 'blink',
        animationDuration: `${this.duration}s`,
        animationIterationCount: `${this.iterationCount}`
        // `${this.duration}s ${this.iterationCount} normal blink`
      }
    }
  }
}
</script>

<style>
/* #vue-blink-enter-active {
  animation-name: blink;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  visibility: visible;
} */

@keyframes blink {
  1% {
    visibility: visible;
  }

  50% {
    visibility: hidden;
  }

  100% {
    visibility: hidden;
  }
}
</style>



